  border-bottom: 1px dashed var(--border-color);
  padding: 16px 0;
}

.world-book-label {
  display: flex;
  align-items: center;
  gap: 8px;
  font-weight: 600;
  color: var(--text-secondary);
  margin-bottom: 12px !important;
}

.world-book-entries {
  max-height: 200px;
  overflow-y: auto;
  padding: 4px;
}

.world-book-entry {
  background-color: var(--surface-color);
  border: 1px solid var(--border-color);
  border-radius: 6px;
  padding: 10px;
  margin-bottom: 8px;
  font-size: 0.9em;
}

.world-book-entry:last-child {
  margin-bottom: 0;
}

.wb-keys {
  color: var(--text-muted);
  margin-bottom: 6px;
  font-size: 0.9em;
}

.wb-keys strong {
  color: var(--text-secondary);
}

.wb-content {
  color: var(--text-primary);
  white-space: pre-wrap;
}

.wb-entry-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 6px;
}

.wb-keys {
  flex: 1;
  margin-bottom: 0;
  padding-right: 10px;
}

.wb-entry-toggle {
  position: relative;
  display: inline-block;
  width: 40px;
  height: 22px;
}

.wb-entry-toggle input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--border-color);
  transition: .4s;
  border-radius: 22px;
}

.slider:before {
  position: absolute;
  content: "";
  height: 16px;
  width: 16px;
  left: 3px;
  bottom: 3px;
  background-color: white;
  transition: .4s;
  border-radius: 50%;
}

input:checked + .slider {
  background-color: #10b981;
}

input:checked + .slider:before {
  transform: translateX(18px);
}

.message {
  display: flex;
  align-items: flex-end;
  gap: 10px;
  margin-bottom: 15px;
  max-width: 100%;
}

.message-avatar {
  width: 36px;
  height: 36px;
  border-radius: 50%;
  overflow: hidden;
  flex-shrink: 0;
  border: 2px solid var(--border-color);
}

.message-avatar img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.user-message {
  align-self: flex-end;
  flex-direction: row-reverse;
}

.ai-message {
  align-self: flex-start;
}

/* --- 訊息編輯功能樣式 --- */
.message-content {
  position: relative; /* 為了讓編輯按鈕可以定位 */
}

/* 新增一個 wrapper，讓它可以接收點擊事件，並且相對定位 */
.bubble-wrapper {
  position: relative;
  cursor: pointer; /* 讓滑鼠移上去時顯示手指圖示，提示可以點擊 */
}

.edit-message-btn {
  position: absolute;
  /* ✨ 核心修改：從底部改到頂部對齊 ✨ */
  bottom: 45px; 
  background: var(--surface-color);
  border: 1px solid var(--border-color);
  color: var(--text-muted);
  border-radius: 50%;
  width: 28px;
  height: 28px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  opacity: 0; /* 平時透明 */
  visibility: hidden; /* 平時隱藏，無法點擊 */
  transform: scale(0.8);
  transition: all 0.2s ease;
  font-size: 14px;
}

.edit-message-btn.visible {
  opacity: 1;
  visibility: visible;
  transform: scale(1);
}

.user-message .edit-message-btn {
  left: -40px;
}

.ai-message .edit-message-btn {
  right: -40px;
}

.edit-container {
  width: 100%;
}

.edit-textarea {
  width: 100%;
  min-height: 150px;
  border: 2px solid var(--primary-color);
  border-radius: 8px;
  padding: 10px;
  font-family: inherit;
  font-size: inherit;
  background-color: var(--background-color);
  color: var(--text-primary);
  resize: vertical;
}

.edit-actions {
  display: flex;
  justify-content: flex-end;
  gap: 8px;
  margin-top: 8px;
}

.edit-btn {
  padding: 6px 14px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-weight: 500;
}

.edit-btn.save {
  background-color: var(--primary-color);
  color: white;
}

.edit-btn.cancel {
  background-color: var(--surface-color);
  color: var(--text-secondary);
}

/* Custom Focus Styles */
input:focus,
textarea:focus,
select:focus {
  outline: none;
  box-shadow: 0 0 0 0.5px var(--accent-color); 
  border-color: var(--accent-color); 
}

/* Pin Chat Button */
.pin-chat-btn {
  background: none;
  border: none;
  font-size: 20px;
  cursor: pointer;
  opacity: 0.5;
  transition: all 0.2s ease;
  padding: 8px;
}
.pin-chat-btn:hover {
  opacity: 1;
  transform: scale(1.2);
}
.pin-chat-btn.active {
  opacity: 1;
  color: #ffc107;
}

/* Version Control & Action Toolbar Styles */
.message-actions-toolbar {
  position: absolute;
  bottom: -32px;
  left: 50%;
  transform: translateX(-50%);
  background: var(--surface-color);
  border: 1px solid var(--border-color);
  border-radius: 16px;
  padding: 2px 4px;
  display: flex;
  align-items: center;
  gap: 4px;
  box-shadow: 0 2px 8px var(--shadow-light);
  transition: all 0.2s ease;
  z-index: 10;
  opacity: 0;
  visibility: hidden;
}

.bubble-wrapper .message-actions-toolbar {
    opacity: 1;
    visibility: visible;
}

.action-btn {
  background: none;
  border: none;
  color: var(--text-muted);
  cursor: pointer;
  width: 28px;
  height: 28px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5em;
  line-height: 1;
  transition: all 0.2s ease;
}

.action-btn:hover:not(:disabled) {
  background: var(--background-color);
  color: var(--text-primary);
}

.action-btn:disabled {
  color: var(--border-color);
  cursor: not-allowed;
}

.version-indicator {
  font-size: 0.75em;
  color: var(--text-muted);
  padding: 0 4px;
  user-select: none;
}

.regenerate-btn {
  position: absolute;
  bottom: 4px;
  background: var(--surface-color);
  border: 1px solid var(--border-color);
  color: var(--text-muted);
  border-radius: 50%;
  width: 28px;
  height: 28px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  opacity: 0;
  visibility: hidden;
  transform: scale(0.8);
  transition: all 0.2s ease;
  font-size: 14px;
  right: -40px;
}

.regenerate-btn.visible,
.bubble-wrapper .regenerate-btn {
    opacity: 1;
    visibility: visible;
    transform: scale(1);
}

/* =================================== */
/*  輸入框選單樣式 (Input Menu)       */
/* =================================== */

/* 使用 wrapper 來做相對定位，這樣選單才能正確地顯示在輸入框上方 */
.input-area-wrapper {
  position: relative;
  /* 讓 wrapper 的背景和邊線與 input-area 保持一致 */
  background: var(--background-color);
  border-top: 1px solid var(--border-color);
}

/* 我們把原本 input-area 的 padding 和 gap 移到 wrapper 裡的 input-area */
.input-area {
  display: flex;
  align-items: center; /* 讓所有東西垂直置中 */
  padding: 15px;
  gap: 10px;
}

/* ✨ 關鍵修改：移除 input-area 的背景和邊線，避免重複 ✨ */
.input-area-wrapper .input-area {
  background: none;
  border-top: none;
  padding: 0; /* 內部的 padding 也由 wrapper 控制 */
}
.input-area-wrapper {
    padding: 15px;
}


/* + 按鈕的樣式 */
.input-menu-btn {
  background: none;
  border: none;
  color: var(--text-muted);
  width: 45px;
  height: 45px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  border-radius: 50%;
  transition: all 0.3s ease;
  flex-shrink: 0; /* 防止按鈕被壓縮 */
}

.input-menu-btn:hover {
  background-color: var(--surface-color);
  color: var(--text-primary);
}

/* 當選單打開時，+ 按鈕的樣式 */
.input-menu-btn.open {
  transform: rotate(45deg);
  background-color: var(--primary-color);
  color: white;
}

/* 選單本體的樣式 */
.input-menu {
  position: absolute;
  bottom: 80px; /* 顯示在輸入框區域的上方，80px 是一個起始值，可以微調 */
  left: 15px;
  background-color: var(--background-color);
  border: 1px solid var(--border-color);
  border-radius: 12px;
  box-shadow: 0 4px 12px var(--shadow-medium);
  padding: 8px;
  display: flex;
  flex-direction: column;
  gap: 8px;
  z-index: 10;
  animation: slideInUp 0.2s ease-out; /* 重複利用您寫好的動畫 */
}

.input-menu-item {
  background: none;
  border: none;
  color: var(--text-secondary);
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 10px 12px;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.2s ease;
  text-align: left;
  font-size: 0.9em;
  width: 100%; /* 讓按鈕填滿選單寬度 */
}

.input-menu-item:hover {
  background-color: var(--surface-color);
  color: var(--text-primary);
}